.Passwordppl-section-content {
    background-color: #f5f5f5;
    padding: 20px;
    margin-top: 3em;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    max-width: 600px;
    margin: 3em auto;
  }
  
  .section-title {
    font-size: 1.5rem;
    margin-bottom: 1rem;
    text-align: center;
  }
  
  .info-card {
    background-color: white;
    padding: 1.5rem;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .info-group {
    margin-bottom: 1.5rem;
  }
  
  .info-item {
    display: flex;
    flex-direction: column;
    margin-bottom: 1rem;
  }
  
  .info-label {
    font-weight: bold;
    margin-bottom: 0.5rem;
  }
  
  .info-value {
    padding: 0.5rem;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .radio-group {
    display: flex;
    justify-content: center;
    margin-bottom: 1rem;
  }
  
  .radio-option {
    margin-right: 1rem;
  }
  
  .submit-btn {
    display: block;
    width: 100%;
    padding: 0.75rem;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    text-align: center;
    font-size: 1rem;
  }
  
  .submit-btn:hover {
    background-color: #0056b3;
  }
  
  .button-group {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-top: 1rem;
  }
  
  .info-result {
    text-align: center;
  }
  